<template>
  <div>
    <el-dialog
    :visible.sync="dialogVisible"
    width="30%"
    :before-close="close" title="">
    <el-form :model="receipt" label-width="100px">
    <el-form-item label=":">
      <el-input placeholder="" v-model="receipt.receivable.code" disabled></el-input>
    </el-form-item>
    <el-form-item label=":">
      <el-input placeholder="" v-model.number="receipt.amount" autofocus></el-input>
    </el-form-item>
    <el-form-item label=":">
      <el-input placeholder="" v-model="receipt.extend"  @keyup.native.enter="edit_confirm"></el-input>
    </el-form-item>
    </el-form>
    <span slot="footer">
      <el-button @click="close"> </el-button>
      <el-button type="primary" @click="edit_confirm"> </el-button>
    </span>
    </el-dialog>
    <el-table
    :data="receipts"
    border
    style="width: 100%"
    size="small">
    <el-table-column
      fixed
      align="center"
      prop="date"
      label=""
      :formatter="formatter">
    </el-table-column>
    <el-table-column
      align="center"
      prop="receivable.code"
      label="">
    </el-table-column>
    <el-table-column
      align="center"
      prop="amount"
      label="">
    </el-table-column>
    <el-table-column
      align="center"
      prop="extend"
      label="">
    </el-table-column>
    <el-table-column
      align="center"
      fixed="right"
      label=""
      width="200">
      <template slot-scope="scope">
        <el-button-group>
          <el-button  type="text" size="small" @click="edit(scope)"></el-button>
          <el-button  type="text" size="small" @click="del(scope)"></el-button>
        </el-button-group>
      </template>
    </el-table-column>
  </el-table>
  <el-pagination
  background
  layout="prev, pager, next"
  :total="totalElements"
  @current-change="currentChange">
  </el-pagination>
  </div>
</template>
<script>
import {getRequest, deleteRequest, putRequest} from '../../utils/axios.js'
import {formatDateTime} from '../../utils/formatDateTime.js'
export default {
  data () {
    return {
      dialogVisible: false,
      // 
      totalElements: null,
      data: {page: 0, size: 10},
      receipts: [],
      receipt: {
        id: 0,
        receivable: {id: 0,
          code: '',
          amountReceived: 0,
          amount: 0,
          state: '',
          extend: '',
          date: 0,
          dealer: ''},
        detail: {id: 0, date: 0, code: '', trader: '', income: 0, pay: 0, type: '', extend: ''},
        date: 0,
        amount: 0,
        extend: ''
      },
      price: 0
    }
  },
  methods: {
    init () {
      this.data.page = 0
      this.jump()
    },
    jump () {
      getRequest('/financeReceiptAPI/all', this.data)
        .then(resp => {
          this.receipts = resp.data.extend.pageInfo.content
          this.totalElements = resp.data.extend.pageInfo.totalElements
        })
        .catch(error => {
          console.log(error)
          this.$message.error('')
        })
    },
    currentChange (currentPage) {
      this.data.page = currentPage - 1
      this.jump()
    },
    close () {
      this.dialogVisible = false
      this.jump()
    },
    edit_confirm () {
      this.receipt.receivable.amountReceived = this.receipt.receivable.amountReceived - this.price + this.receipt.amount
      this.receipt.date = new Date()
      if (this.receipt.receivable.amountReceived >= this.receipt.receivable.amount) {
        this.receipt.receivable.state = 'COMPLETE'
      } else {
        this.receipt.receivable.state = 'INCOMPLETE'
      }
      this.receipt.detail.date = this.receipt.date
      this.receipt.detail.extend = this.receipt.extend
      this.receipt.detail.pay = 0
      this.receipt.detail.income = this.receipt.amount
      putRequest('/financeReceiptAPI/', this.receipt)
        .then(resp => {
          this.jump()
          this.dialogVisible = false
        })
        .catch(error => {
          console.log(error)
          this.$message.error('')
        })
    },
    del (scope) {
      this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'error'
      }).then(() => {
        scope.row.receivable.amountReceived = scope.row.receivable.amountReceived - scope.row.amount
        if (scope.row.receivable.amountReceived >= scope.row.receivable.amount) {
          scope.row.receivable.state = 'COMPLETE'
        } else {
          scope.row.receivable.state = 'INCOMPLETE'
        }
        putRequest('/financeReceiptAPI/', scope.row)
          .then(resp => {
            deleteRequest('/financeReceiptAPI/', scope.row)
              .then(resp => {
                this.jump()
                this.$message({
                  type: 'success',
                  message: '!'
                })
              })
              .catch(error => {
                console.log(error)
                this.$message.error('')
              })
          })
          .catch(error => {
            console.log(error)
            this.$message.error('')
          })
      }).catch(() => {
        this.$message({
          type: 'info',
          message: ''
        })
      })
    },
    edit (scope) {
      this.receipt = scope.row
      this.price = this.receipt.amount
      this.dialogVisible = true
    },

    formatter (row, column, cellValue) {
      return formatDateTime(cellValue)
    }
  },
  mounted () {
    this.init()
  }
}
</script>
<style scoped>
  div{
    text-align: left
  }
  .el-pagination{
    margin-top: 30px;
    text-align: center;
  }
  .el-button-group .el-button{
    margin-left: 15px;
    margin-right: 15px
  }
</style>


